<template>
  <div>
    <div class="info_info">
      <div class="info_base">
        <section class="person_info_base">
          <span>姓名: <strong>{{name}}</strong></span>
          <span>电话: <strong>{{tel}}</strong></span>
          <span>邮政编码: <strong>{{id}}</strong></span>
        </section>
        <div class="address">
          <span style="padding: 0 0.2rem;display:inline-block;">地址: <strong>{{address}}</strong></span>
        </div>
      </div>
      <div class="goods_list">
        <section class="list_header">
          <span class="item">订单号: <strong>{{order}}</strong></span>
          <span class="item">订单状态: <strong>{{state}}</strong></span>
        </section>
      </div>
      <ul class="goods">
        <li style="display: flex;justify-content: space-between;align-items: center" v-for="(item,key) in goodsList">
          <div class="left">
            <img :src="item.pic" alt="" class="photo_img">
            <p class="left_info">
              <span>{{item.name}}</span>
              <span><strong>x{{item.num}}</strong> <strong style="display: inline-block;margin-left: 0.1rem">{{item.spec_value}}</strong></span>
              <span>¥{{item.price}}</span>
            </p>
          </div>
        </li>
      </ul>
      <div>
        <div class="fahuo" v-if = "state === '未发货'">
          <Button type="info" @click.native="modal1 = true">发货</Button>
          <Modal v-model="modal1" @on-ok="ok" @on-cancel="cancel" width="400" :closable="false">
            <p>
              <Select v-model="model">
                <Option v-for="item in cityList" :value="item.coding" :key="item.coding">{{ item.name }}</Option>
              </Select>
            </p>
            <p>
              <Input v-model="value" placeholder="请输入快递单号"></Input>
            </p>
          </Modal>
          <Button type="warning" @click.native="refund(item)">退款</Button>
        </div>
      </li>
    </ul>
        <div class="fahuo" v-if = "state === '已发货'">
          <p style="font-size: 16px; color: red;">已发货</p>
        </div>
         <div class="fahuo" v-if = "state === '已退款'">
          <p style="font-size: 16px; color: red;">正在退款中...</p>
        </div>
        <div class="fahuo" v-if = "state === '已完成'" >
          <p style="font-size: 16px; color: red;">订单已完成</p>
        </div>
      </div>
  </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        name: "",
        tel: '',
        email: '',
        id: "",
        address: "",
        order: "",
        state: "",
        goodsList: [],
        order_id: '',
        goodsOrder: '',
        modal1: false,
        value: '',
        cityList: [{
            name: '顺丰速运',
            coding: 'SF',
          }, {
            name: '百世快递',
            coding: 'HTKY',
          }, {
            name: '中通快递',
            coding: 'ZTO',
          }, {
            name: '申通快递',
            coding: 'STO',
          },
          {
            name: '圆通速递',
            coding: 'YTO',
          }, {
            name: '韵达速递',
            coding: 'YD',
          },
          {
            name: '邮政快递包裹',
            coding: 'YZPY'
          },
          {
            name: 'EMS',
            coding: 'EMS',
          }, {
            name: '天天快递',
            coding: 'HHTT',
          },
          {
            name: '京东物流',
            coding: 'JD'
          },
          {
            name: '全峰快递',
            coding: 'QFKD',
          }, {
            name: '国通快递',
            coding: 'GTO',
          },
          {
            name: '优速快递',
            coding: 'UC'
          },
          {
            name: '德邦',
            coding: 'DBL'
          },
          {
            name: '快捷快递',
            coding: 'FAST'
          },
          {
            name: '亚马逊',
            coding: 'AMAZON'
          },
          {
            name: '宅急送',
            coding: 'ZJS'
          }
        ],
        model: ''
      }
    },
    methods: {
      ok() {
        this.handelOrder(2)
      },
      cancel() {

      },
      //    封装
      handelOrder(state, order) {
        this.post("custom/Order/updateOrderState", {
          appid: this.$route.params.appid,
          state: state,
          order_id: this.order_id,
          kd_number: this.value,
          kd_code:this.model
        }).then(res => {
          if(res.data.code === 10000) {
            this.$Modal.success({
              title: "成功",
              content: res.data.msg_test
            });
          } else {
             this.$Modal.error({
              title: "失败",
              content: res.data.msg_test
            });
          }
        })
      },
      send(item) {
      },
      refund(item) {
        this.handelOrder(4)
      }
    },
    mounted() {
      this.$store.commit('setStatus', false);
      this.order_id = this.$route.params.id;
      this.post("custom/Order/getOrderById", {
        order_id: this.order_id
      }).then(res => {
        if(res.data.code === 10000) {
          this.name = res.data.data[0].username;
          this.tel = res.data.data[0].tel;
          this.email = res.data.data[0].mail;
          this.id = res.data.data[0].zipcode;
          this.address = res.data.data[0].address;
          this.order = res.data.data[0].order_sn;
          this.state = res.data.data[0].state;
          if(this.state === 1) {
            this.state = "未发货"
          } else if(this.state === 2) {
            this.state = "已发货"
          } else if(this.state === 3) {
            this.state = "已完成"
          } else if(this.state === 4) {
            this.state = "已退款"
          }else if(this.state === 0){
            this.state = "未支付"
          }
          this.goodsList = res.data.data;
          this.goodsList.forEach(item => {
            return item.pic = "http://weapp.xiguawenhua.com/" + item.pic
          })
        } else {
          alert(res.data.msg_test);
        }
      })
    }
  }
</script>
<style scoped>
  .top {
    padding: 0.1rem 0;
    border-bottom: 1px solid #ebecee
  }

  header {
    display: flex;
    align-items: center;
  }

  header>span {
    font-size: 0.18rem;
    display: inline-block;
    margin-left: 0.1rem
  }

  p {
    padding: 0.1rem 0;
    font-size: 0.14rem;
    letter-spacing: 0.01rem
  }

  .info {
    padding: 0.1rem 0;
  }

  .person_info_base {
    background-color: white;
    padding: 0.2rem 0.2rem;
    border-bottom: 1px solid #EEEEEE;
  }

  .person_info_base>span {
    display: inline-block;
    padding: 0 1rem;
  }

  .person_info_base>span>strong {
    display: inline-block;
    margin-left: 0.05rem
  }

  .person_info_base>span:nth-child(1) {
    display: inline-block;
    padding: 0 0.2rem;
  }

  .address {
    padding: 0.2rem 0.2rem;
    margin-top: 0.01rem
  }

  .goods_list {
    margin-top: 0.2rem
  }

  .list_header {
    padding: 0.2rem 0.2rem;
    background-color: #F9FAFB;
    display: flex;
    justify-content: space-between
  }

  .item {
    color: #4E4E4E;
    padding: 0 0.2rem
  }

  .goods {
    padding: 0.2rem 0.2rem;
    border: 1px solid #f3f3f3;
    background: white;
  }

  .photo_img {
    width: 0.88rem;
    max-height: 0.8rem;
    margin: 0 0.2rem
  }

  .left {
    display: flex;
    justify-content: flex-start;
    align-items: center
  }

  .left_info {
    display: flex;
    flex-direction: column;
    margin-left: 0.1rem
  }

  .left_info>span {
    display: inline-block;
    padding: 0.02rem 0;
  }

  .right {
    padding: 0 0.2rem
  }

  .fahuo {
    margin-top: 20px;
    float: right;
    margin-right: 40px;
  }
  .info_base{
    background: white;
    box-shadow: 5px 5px 10px #E7E7E7;
  }
</style>



